import LeftMenuBtn from "../ui/LeftMenuBtn/LeftMenuBtn"
import Layout from "./layout"

const btnIconArr = [
    './menu.svg',
    './progress.svg',
    './plus.svg',
    './about.svg',
    './logout.svg'
]

function LeftMenu() {
    return (
        <Layout>
            <div className="flex flex-col gap-[24px] items-center justify-start border-1 w-15 h-screen">
                <ul className="flex flex-col items-center w-15">
                    {btnIconArr.map((item) => (
                        <li key={item} className="flex items-center justify-center">
                            <LeftMenuBtn>
                                <img className="w-8 h-8 opacity-80" src={item}/>
                            </LeftMenuBtn>
                        </li>
                    ))}
                </ul>
            </div>
        </Layout>
    )
}

export default LeftMenu